.map {
  position: relative;
  height: 350px;
  background-color: $photo-background;
  overflow: hidden;
  margin-bottom: 10px;
}

.map__iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;

  &--nojs {
    display: none;
  }

  & img {
    width: 100%;
    display: block;
  }
}
